import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { getData } from "../../../config/axios";
import ValidasiUangKas from "../../../Validasi/UangKas/ValidasiUangKas";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: " ,-",
  locale: "id-ID",
});
class HeadAmbilUangKas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      parametertransaksi: [],
    };
  }

  componentDidMount() {
    getData("paratransaksi/get/all")
      .then((res) => {
        this.setState({
          parametertransaksi: res.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="kategori"
              component={ReanderSelect}
              options={this.state.parametertransaksi.map((list) => {
                let data = {
                  value: list.kode_transaksi,
                  name: list.kode_transaksi,
                };
                return data;
              })}
              label="Kategori"
              placeholder="Masukan Kategori"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="jumlah"
              component={ReanderField}
              type="telp"
              label="Jumlah"
              placeholder="Masukan Jumlah"
              {...currencyMask}
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="keterangan"
              component={ReanderField}
              type="text"
              label="Keterangan"
              placeholder="Masukan Keterangan"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary">
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Menyimpan
                  </>
                ) : (
                  <>
                    Simpan <i className="fa fa-paper-plane ml-3"></i>
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

HeadAmbilUangKas = reduxForm({
  form: "HeadAmbilUangKas",
  enableReinitialize: true,
  validate: ValidasiUangKas,
})(HeadAmbilUangKas);
export default connect()(HeadAmbilUangKas);
