const ValidasiUangKas = (value) => {
    const errors = {};
    if (!value.kategori) {
        errors.kategori = "Kategori Tidak Boleh Kosong";
    }
    if (!value.jumlah) {
        errors.jumlah = "Jumlah Tidak Boleh Kosong";
    }

    if (!value.keterangan) {
        errors.keterangan = "Keterangan Tidak Boleh Kosong";
    }

    return errors;
}

export default ValidasiUangKas;


