import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reset } from "redux-form";
import { NotifInfo, NotifSucces } from "../../../components/helpers/notification";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel";
import { postData } from "../../../config/axios";
import HeadAmbilUangKas from "./HeadAmbilUangKas";

class AmbilUangKas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading:false
    };
  }

  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      kategori : data.kategori,
      jumlah_rp: data.jumlah,
      deskripsi: data.keterangan
    }
    postData("cash/out", data_post)
      .then(() => {
        NotifSucces("Data Berhasil Disimpan");
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .then(() => {
        this.props.dispatch(reset("HeadAmbilUangKas"));
      })
      .catch((error) =>
        this.ErrorHandling(error)
    );
  }
  ErrorHandling(err) {
    NotifInfo(err)
    this.setState({ isLoading: false });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Transaksi Kas</Link>
          </li>
          <li className="breadcrumb-item active">Ambil Uang Kas</li>
        </ol>
        <h1 className="page-header">Ambil Uang Kas </h1>
        <Panel>
          <PanelHeader>Ambil Uang Kas</PanelHeader>
          <PanelBody>
            <HeadAmbilUangKas isLoading={this.state.isLoading} onSubmit={(data) => this.handleSubmit(data)} />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

export default connect()(AmbilUangKas);

